import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { ProductLibraryService } from '../../../../../services/productLibrary/product-library.service';
import { TicketService } from '../../../../../services/ticket/ticket.service';
import jwt_decode from 'jwt-decode';

@Component({
  selector: 'app-unit-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss'],
})
export class UnitDetailComponent implements OnInit {

  @Output() close = new EventEmitter<any>();
  @Output() upload = new EventEmitter<any>();
  @Input() action: string;
  @Input() unitId: string;

  dataList: Array<any> = [];

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private productLibraryService: ProductLibraryService,
    private ticketService: TicketService
  ) {}

  ngOnInit(): void {
    // const token = JSON.parse(localStorage.getItem('user'))?.accessToken;
    // this.decoded = jwt_decode(token);
    // console.log(this.decoded);

    console.log(this.action);
    if (this.action === 'detail') {
      this.getUnitById ();
    }
  }

  Arr = Array;
  
  unit = new FormGroup(
    {
      unit_short_name: new FormControl('', [Validators.required]),
      unit_long_name: new FormControl('', [Validators.required]),
    },
    { updateOn: 'change' }
  );

  get unit_short_name() {
    return this.unit.get('unit_short_name');
  }

  get unit_long_name() {
    return this.unit.get('unit_long_name');
  }

  onSubmit() {
    const group: FormGroup = this.unit as FormGroup;
    console.log(group);
    if (this.unit.valid) {
      console.log(group.value);
    
      this.spinner.show();
      this.productLibraryService.createUnit(group.value).subscribe(
        (data) => {
          console.log(data); 
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  update() {
    console.log('updating........');

    const group: FormGroup = this.unit as FormGroup;
    console.log(group);
    console.log(group.valid);
    if (group.valid) {
      console.log(group.value);
      this.spinner.show();
      this.productLibraryService.updateUnit(group.value, this.unitId).subscribe(
        (data) => {
          console.log(data);
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }
  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  getUnitById() {
    this.spinner.show();
    this.productLibraryService.getUnitById(this.unitId).subscribe((data) => {
      console.log(data);
      this.spinner.hide();
      this.fillFormFields(data.body);
    });
  }

  fillFormFields(unit) {
    console.log(unit)
    const group: FormGroup = this.unit as FormGroup;
    group.controls.unit_short_name.setValue(unit.unit_short_name || '');
    group.controls.unit_long_name.setValue(unit.unit_long_name || '');

  }

}
