import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { ProductLibraryService } from './../../../../services/productLibrary/product-library.service';

@Component({
  selector: 'app-unit',
  templateUrl: './unit.component.html',
  styleUrls: ['./unit.component.scss']
})
export class UnitComponent implements OnInit {


  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private router: Router,
    private productLibraryService: ProductLibraryService
  ) {
    // this.socketService.setupSocketConnection();
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      // totalItems: this.customerData.length,
    };

    // this.customerData = this.customerData.slice();
    // this.getUsersSuggestion();
  }

  ngOnInit(): void {
    this.getUnits();
    // this.getAdminUsers();
  }
  action;
  unitId;
  isAddNewUnit = false;
  units: any = [];

  fields: any = [
    { label: 'Short Name', fieldName: 'unit_short_name' },
    { label: 'Long Name', fieldName: 'unit_long_name' },
  ];
  p: number = 1;
  config: any;
  pageSizes = [1, 2, 5, 10, 25, 50];
  sort = {
    orderBy: 'unit_long_name',
    sort: 'ASC',
  };

  pageChanged(event) {
    this.config.currentPage = event;
    this.getUnits();
  }

  changePageSize() {
    this.getUnits();
  }

  library: any;
  keyword = 'name';
  search = [];

  selectEvent(item) {
    this.search = [item];
  }

  onChangeSearch(search: string) {
    // this.sendUserSearch(search);
  }
  onFocused(e) {
    // do something
  }

  onEnter(e) {
    const userIds = [];
    this.search.map((item) => {
      userIds.push(item.id);
    });
    this.config['userIds'] = userIds;
    console.log(this.config);
    this.getUnits();
  }

  sortBy(orderBy, sort) {
    this.sort = {
      orderBy,
      sort,
    };
    this.getUnits();
  }

  getUnits() {
    this.spinner.show();

    this.productLibraryService.getUnitByPagination(this.config, this.sort).subscribe(
      (data) => {
        console.log(data);
        this.units = data.data;
        this.config.totalItems = data.total;
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  deleteUnit(unitId, index) {
    this.spinner.show();

    this.productLibraryService.deleteUnitById(unitId).subscribe(
      (data) => {
        this.units.splice(index, 1);
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  addNewUnit() {
    this.action = 'create';
    this.isAddNewUnit = !this.isAddNewUnit;
  }

  unitDetail(unitId) {
    this.action = 'detail';
    this.unitId = unitId;
    this.isAddNewUnit = !this.isAddNewUnit;
  }
  // sendUserSearch(keyword) {
  //   const obj = {
  //     keyword,
  //     type: ['customer', 'merchant'],
  //     limit: 10,
  //   };
  //   this.socketService.sendUserMessage(obj);
  // }

  // getUsersSuggestion() {
  //   this.socketService.getSuggestion().subscribe((data) => {
  //     console.log(data);
  //     this.library = data;
  //     this.search = data;
  //   });
  // }

  exportColumns: any[];
  exportLst: ExportCustomer[];
  //--------export to pdf start-----------------

  exportPdf() {
    import('jspdf').then((jsPDF) => {
      import('jspdf-autotable').then((x) => {
        const doc = new jsPDF.default(0, 0);

        this.exportLst = [];

        if (this.units) {
          this.units.forEach((data) => {
            this.exportLst.push({
              FirstName: data.firstName,
              LastName: data.lastName,
              Email: data.email,
              Phone: data.phoneNumber,
              Address: data.address1,
              City: data.city,
              Date: data.createdAt,
            });
          });

          // this.driverService.exportDriversFilteredRecords(this.driverDetails).subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });

          doc.autoTable(this.exportColumns, this.exportLst);
          // });
        } else {
          // this.spinner.show();
          // this.driverService.exportAllDrivers().subscribe(data => {
          //   data.lstDrivers.forEach((i) => {
          //     this.exportLst.push({
          //       FirstName: i.FirstName, LastName: i.LastName,
          //       EmailAddress: i.EmailAddress, PhoneNumber: i.PhoneNumber
          //     })
          //   });
          //   this.spinner.hide();
          //   this.exportExcel(this.exportLst);
          // });
        }

        //doc.autoTable(this.exportColumns, this.lstDrivers);
        doc.save('CustomerTable.pdf');
      });
    });
  }
  //--------export to pdf end-----------------

  //----------export to excel----start-----------------------
  exportToExcel() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.units) {
      this.units.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportExcel(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportExcel(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsExcelFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsExcelFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.xlsx';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to excel----end-------------------------

  //---------------------------------------------
  //----------export to CSV----start-----------------------
  exportToCSV() {
    //---------------------------------------------------------------
    this.exportLst = [];

    if (this.units) {
      this.units.forEach((data) => {
        this.exportLst.push({
          FirstName: data.firstName,
          LastName: data.lastName,
          Email: data.email,
          Phone: data.phoneNumber,
          Address: data.address1,
          City: data.city,
          Date: data.createdAt,
        });
      });

      this.exportCSV(this.exportLst);
      // });
    } else {
    }
    //---------------------------------------------------------------
  }

  //--------------------------
  exportCSV(exportLst: ExportCustomer[]) {
    import('xlsx').then((xlsx) => {
      if (this.exportLst.length > 0) {
        console.log(exportLst);
        const worksheet = xlsx.utils.json_to_sheet(exportLst);
        const workbook = { Sheets: { data: worksheet }, SheetNames: ['data'] };
        const excelBuffer: any = xlsx.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsCSVFile(excelBuffer, 'customersDetails');
      }
    });
  }
  //--------------------------

  //--------------------------
  saveAsCSVFile(buffer: any, fileName: string): void {
    import('file-saver').then((FileSaver) => {
      let EXCEL_TYPE =
        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
      let EXCEL_EXTENSION = '.csv';
      const data: Blob = new Blob([buffer], {
        type: EXCEL_TYPE,
      });
      FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
    });
  }
  //--------------------------
  //----------export to CSV----end-------------------------
  //---------------------------------------------
}

export interface ExportCustomer {
  FirstName: string;
  LastName: string;
  Email: string;
  Phone: number;
  Address: string;
  City: number;
  Date: number;
}
