import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { ProductLibraryService } from '../../../../../services/productLibrary/product-library.service';
import { TicketService } from '../../../../../services/ticket/ticket.service';
import jwt_decode from 'jwt-decode';

@Component({
  selector: 'app-ethnicity-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class EthnicityDetailComponent implements OnInit {

  @Output() close = new EventEmitter<any>();
  @Output() upload = new EventEmitter<any>();
  @Input() action: string;
  @Input() ethnicityId: string;

  dataList: Array<any> = [];

  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private productLibraryService: ProductLibraryService,
    private ticketService: TicketService
  ) {}

  ngOnInit(): void {
    // const token = JSON.parse(localStorage.getItem('user'))?.accessToken;
    // this.decoded = jwt_decode(token);
    // console.log(this.decoded);

    console.log(this.action);
    if (this.action === 'detail') {
      this.getEthnicityById ();
    }
  }

  Arr = Array;
  
  ethnicity = new FormGroup(
    {
      ethnicity_name: new FormControl('', [Validators.required]),
    },
    { updateOn: 'change' }
  );

  get ethnicity_name() {
    return this.ethnicity.get('ethnicity_name');
  }

  onSubmit() {
    const group: FormGroup = this.ethnicity as FormGroup;
    console.log(group);
    if (this.ethnicity.valid) {
      console.log(group.value);
    
      this.spinner.show();
      this.productLibraryService.createEthnicity(group.value).subscribe(
        (data) => {
          console.log(data); 
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }

  update() {
    console.log('updating........');

    const group: FormGroup = this.ethnicity as FormGroup;
    console.log(group);
    console.log(group.valid);
    if (group.valid) {
      console.log(group.value);
      this.spinner.show();
      this.productLibraryService.updateEthnicity(group.value, this.ethnicityId).subscribe(
        (data) => {
          console.log(data);
          this.spinner.hide();
        },
        (error) => {
          this.toastr.error(error.error.message, 'Error');
          this.spinner.hide();
        }
      );
    } else {
      this.validateAllFormFields(group);
    }
  }
  validateAllFormFields(formGroup: FormGroup) {
    Object.keys(formGroup.controls).forEach((field) => {
      const control = formGroup.get(field);
      if (control instanceof FormControl) {
        control.markAsTouched({ onlySelf: true });
      } else if (control instanceof FormGroup) {
        this.validateAllFormFields(control);
      }
    });
  }

  ethnicities: any = [];
  brands: any = [];
  units: any = [];

  getEthnicityById() {
    this.spinner.show();
    this.productLibraryService.getEthnicityById(this.ethnicityId).subscribe((data) => {
      console.log(data);
      this.spinner.hide();
      this.fillFormFields(data.body);
    });
  }

  fillFormFields(ethnicity) {
    console.log(ethnicity)
    const group: FormGroup = this.ethnicity as FormGroup;
    group.controls.ethnicity_name.setValue(ethnicity.ethnicity_name || '');

  }

}
